/**
 * ì²œë‹¨ìœ„ ì½¤ë§ˆ í˜•ì‹ìœ¼ë¡œ í¬ë§·íŒ…í•©ë‹ˆë‹¤.
 * @param {string} val
 * @param {number} fixed ì†Œìˆ˜ì  í‘œì‹œ ìžë¦¬ìˆ˜, ê¸°ë³¸ê°’ì€ 2ìž…ë‹ˆë‹¤.
 * @returns ì²œë‹¨ìœ„ ì½¤ë§ˆê°€ ë¶™ì€ ìˆ«ìžë¥¼ stringìœ¼ë¡œ ë°˜í™˜í•©ë‹ˆë‹¤.
 */
export function addComma(val, fixed = 2) {
  return Number(val)
    .toFixed(fixed)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

/**
 * YYYYMMDDë¥¼ YYYY-MM-DDë¡œ ë³€í™˜í•©ë‹ˆë‹¤.
 * @param {string} str YYYYMMDD
 * @returns YYYY-MM-DD
 */
export function toDate(str) {
  return str.slice(0, 4) + "-" + str.slice(4, 6) + "-" + str.slice(6, 8);
}

/**
 * formì„ ì œì¶œí•˜ê³  ì„œë²„ì˜ ì‘ë‹µì— ë”°ë¼ ë©”ì‹œì§€ë¥¼ ë„ìš°ê³  ë¦¬ë‹¤ì´ë ‰íŠ¸ ë™ìž‘í•©ë‹ˆë‹¤.
 * @param {element} formElement
 * @returns ìš”ì²­ê²°ê³¼ ê²½ê³ ì°½ ë˜ëŠ” íŽ˜ì´ì§€ ë¦¬ë‹¤ì´ë ‰íŠ¸
 */
export function submitForm(formElement) {
  window.event.preventDefault();

  // í•¨ìˆ˜ í˜¸ì¶œì— ì‚¬ìš©ëœ ë²„íŠ¼ì„ disableë¡œ ìƒíƒœ ë³€ê²½í•©ë‹ˆë‹¤.
  let btn = window.event.target;
  btn.disable = true;

  // formElementì— ì •ì˜ëœ methodê°€ ì—†ìœ¼ë©´ ì—ëŸ¬ë¥¼ ë˜ì§‘ë‹ˆë‹¤.
  if (formElement.method == "") {
    throw new Error("ì œì¶œ ë˜ëŠ” formElementì— methodê°€ ì—†ìŠµë‹ˆë‹¤.");
  }

  // htmlì—ì„œ ì§€ì •í•œ ìœ íš¨ì„± ê²€ì¦ì„ ë¨¼ì € ìˆ˜í–‰í•©ë‹ˆë‹¤.
  if (!formElement.checkValidity()) {
    formElement.reportValidity();
    return;
  }

  let formData = new FormData(formElement);
  let action = formElement.action;
  let xhr = new XMLHttpRequest();

  xhr.open(formElement.method, action);

  xhr.onload = function () {
    let data = JSON.parse(xhr.responseText);

    if (xhr.status == 200 || xhr.status == 201) {
      alert(data["message"] + "(" + xhr.status + ")");

      if (data["redirect_url"] == "previous" && document.referrer == "") {
        return close(); // ì´ì „ ê²½ë¡œë¡œ ì´ë™í•  ë•Œ ì´ì „ ê²½ë¡œê°€ ì—†ìœ¼ë©´ íƒ­ì„ ë‹«ìŠµë‹ˆë‹¤.
      } else if (data["redirect_url"] == "previous") {
        return (window.location.href = document.referrer); // ì´ì „ ê²½ë¡œë¡œ ì´ë™í•©ë‹ˆë‹¤.
      } else if (data["redirect_url"] == "refresh") {
        return window.location.reload(); // íŽ˜ì´ì§€ë¥¼ ìƒˆë¡œê³ ì¹¨ í•©ë‹ˆë‹¤.
      } else if (data["redirect_url"] == "") {
        return;
      } else {
        return (window.location.href = data["redirect_url"]);
      }
    } else {
      return alert(data["message"] + "(" + xhr.status + ")");
    }
  };

  xhr.send(formData);
}

/** Dispatch event on click outside of node */
export function clickOutside(node) {
  const handleClick = (event) => {
    if (node && !node.contains(event.target) && !event.defaultPrevented) {
      node.dispatchEvent(new CustomEvent("click_outside", node));
    }
  };

  document.addEventListener("click", handleClick, true);

  return {
    destroy() {
      document.removeEventListener("click", handleClick, true);
    },
  };
}

export function toggleBoolean(input) {
  return (input = !input);
}
